  <section class="Maison">
    <div class="maisonwrap">
      <div v-for="i in [currentIndex]" :key="i">
        <img class="image" :src="currentImg" />
      </div>
    </div>

    <div>
      <AccordionView title="T'aider à préparer l'arrivée de bébé">
        <p>Tu dois :</p>
        <ul>
          <li>choisir le matériel, mais lequel ?</li>
          <li>Etablir une liste de naissance</li>
          <li>
            Organiser et aménager la chambre, la cuisine, la salle de bains
          </li>
        </ul>
      </AccordionView>
      <AccordionView title="T'aider à prendre soin de bébé">
        <p>Tu as envie d'être prête :</p>
        <ul>
          <li>Pour donner le bain à ton enfant</li>
