          <li>Pour savoir quels produits privilégier</li>
        </ul>
      </AccordionView>
      <AccordionView title="T'initier au toucher massage">
        <p>
          Tu as envie d'entrer en relation différemment avec ton enfant et :
        </p>
        <ul>
          <li>Tu aimerais connaitre les différentes techniques</li>
          <li>Tu voudrais connaitre les produits à utiliser</li>
          <li>Découvrir les astuces et les moments propices</li>
        </ul>
      </AccordionView>
      <AccordionView
        title="Te donner des conseils selon tes besoins, tes choix"
      >
        <p>Je peux t'accompagner autour :</p>
        <ul>
          <li>De ton désir d'enfant</li>
