<script>
import AccordionView from "@/components/accordion/accordionv2.vue";
export default {
  name: "PrestaMomes",
  components: {
    AccordionView,
  },
  data() {
    return {
      images: [
        require("@/assets/img/mere_1.jpeg"),
        require("@/assets/img/mere_2.jpeg"),
        require("@/assets/img/mere_3.jpeg"),
        require("@/assets/img/mere_4.jpeg"),
      ],
      timer: null,
      currentIndex: 0,
    };
  },

  computed: {
    currentImg: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    },
  },

  mounted: function () {
    this.startSlide();
  },

  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 3000);
    },

    next: function () {
      this.currentIndex += 1;
    },
    prev: function () {
      this.currentIndex -= 1;
    },
  },
};
</script>

