<script>
import AccordionView from "@/components/accordion/accordionv2.vue";
export default {
  name: "PrestaLeFoyer",
  components: {
    AccordionView,
  },
  data() {
    return {
      images: [
        require("@/assets/img/femme_peintre.jpeg"),
        require("@/assets/img/preparer-crusine.jpeg"),
        require("@/assets/img/trier_maison.jpeg"),
        require("@/assets/img/zero_dechets.jpeg"),
      ],
      timer: null,
      currentIndex: 0,
    };
  },

  computed: {
    currentImg: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    },
  },

  mounted: function () {
    this.startSlide();
  },

  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 3000);
    },

    next: function () {
      this.currentIndex += 1;
    },
    prev: function () {
      this.currentIndex -= 1;
    },
  },
};
</script>

