import { createRouter, createWebHashHistory } from "vue-router";
import HomeView from "@/views/HomeView.vue";
import MeconnaitreView from "@/views/MeConnaitreView.vue";
import MeconnaitreDeuxView from "@/views/ConditionalViews/Meconnaitre2View.vue";
import ContactView from "@/views/ContactView.vue";
import PrestationsView from "@/views/Prestations.vue";
import RealisationsView from "@/views/Realisations.vue";
import TarifsView from "@/views/Tarifs.vue";
import EssaisView from "@/components/StaticFiles/HomeAll/HomeSuite/MesEssais.vue";
import QuestionnaryHomev2 from "@/views/ConditionalViews/QuestionnaryHomev2.vue";
import QuestionnaryParentview from "@/views/ConditionalViews/QuestionnaryParent.vue";
const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/me-connaitre",
    name: "MeConnaitreView",
    component: MeconnaitreView,
  },
  {
    path: "/me-connaitre-deux",
    name: "MeConnaitreDeuxView",
    component: MeconnaitreDeuxView,
  },

  {
    path: "/Contact",
    name: "ContactView",
    component: ContactView,
  },
  {
    path: "/prestations",
    name: "PrestationsView",
    component: PrestationsView,
  },
  {
    path: "/realisations",
    name: "RealisationsView",
    component: RealisationsView,
  },
  {
    path: "/Tarifs",
    name: "TarifsView",
    component: TarifsView,
  },
  {
    path: "/Essais",
    name: "MesEssaisView",
    component: EssaisView,
  },
  {
    path: "/Questionnaire-maison",
    name: "QuestionnaryHomeView",
    component: QuestionnaryHomev2,
  },
  {
    path: "/Questionnaire-parentalite",
    name: "QuestionnaryParentView",
    component: QuestionnaryParentview,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
