<template>
  <div class="externalwrapper">
    <div class="question" @click="isOpen = !isOpen">
      {{ title }}
    </div>
    <div class="answer" :class="isOpen ? 'd-block' : 'hidden'">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "AccordionView",
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data: () => {
    return {
      isOpen: false,
    };
  },
};
</script>
