<template>
  <div>
    <div>
      <div v-for="i in [currentIndex]" :key="i">
        <img class="imagestyling" :src="currentImg" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ImageSlider",
  data() {
    return {
      images: [
        require("@/assets/img/digit.jpg"),
        require("@/assets/img/dressing1.jpg"),
        require("@/assets/img/armoire_linge.jpg"),
        require("@/assets/img/baby_playing.jpg"),
      ],
      timer: null,
      currentIndex: 0,
    };
  },

  computed: {
    currentImg: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    },
  },

  mounted: function () {
    this.startSlide();
  },

  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 3000);
    },

    next: function () {
      this.currentIndex += 1;
    },
    prev: function () {
      this.currentIndex -= 1;
    },
  },
};
</script>
<style scoped>
.imagestyling {
  @apply h-48 w-48 md:h-64 md:w-64 lg:h-80 lg:w-80 rounded-full shadow-sm grayscale;
}
.fade-enter-from {
  opacity: 0;
}
.fade-enter-to {
  opacity: 1;
}
.fade-enter-active {
  transition: 0.3s ease;
}
.fade-leave-from {
  opacity: 1;
}
.fade-leave-to {
  opacity: 0;
}
.fade-leave-active {
  transition: 0.3s ease;
}
</style>
