<template>
  <button :class="buttonClass">
    {{ buttontext }}
  </button>
</template>

<script>
export default {
  name: "ActionButton",
  props: {
    buttontext: {
      type: String,
      required: true,
      defaults: () => "a definir",
    },
    type: {
      type: String,
      required: true,
      defaults: "contact",
      validator(value) {
        return ["contact", "redirect"].includes(value);
      },
    },
  },
  computed: {
    buttonClass() {
      return {
        [this.type]: true,
      };
    },
  },
};
</script>
<style scoped>
button {
  @apply font-sans text-2xl py-3 font-medium text-white border-0 rounded bg-vert-logo hover:bg-bleu-logo hover:shadow-gris;
}
.contact {
  @apply text-white border-0 rounded bg-black hover:bg-bleu-logo hover:shadow-gris;
}
.redirect {
  @apply text-white  border-0 rounded bg-bleu-logo hover:bg-vert-logo hover:shadow-gris;
}
.Prestations {
  @apply text-white text-4xl border-0 rounded-3xl bg-bleu-logo hover:bg-vert-logo hover:shadow-gris p-10;
}
</style>
