<template>
  <section class="pt-80">
    <div class="internalwrapper">
      <div
        v-for="question in [questions]"
        :key="question.name"
        class="upperlabel"
      >
        <h1 class="titrestyle">{{ currentQuestion }}</h1>
        <spaceBetween />

        <textarea
          v-for="placeholder in [placeholders]"
          :key="placeholder.name"
          v-model="textarea"
          class="textarea"
          :placeholder="currentPlaceholder"
          type="textarea"
          :error="textareaError"
        />
        <h1 class="validator">{{ textareaError }}</h1>
        <spaceBetween />
      </div>
    </div>
    <div class="boutonOrganizer">
      <button
        v-if="currentIndex > 0 || currentIndex < questions.length - 1"
        class="boutonstyleone"
        @click="currentIndex = currentIndex - 1"
      >
        Question précédente
      </button>
      <button
        v-if="currentIndex < questions.length - 1"
        class="boutonstyleone"
        @click="currentIndex = currentIndex + 1"
      >
        Question suivante
      </button>
      <button
        v-if="currentIndex === questions.length - 1"
        class="boutonstyleone"
        @click="currentIndex = currentIndex + 1"
      >
        Soumettre
      </button>
      <spaceBetween />
    </div>

    <spaceBetween />
  </section>
</template>
<script>
import { useField, useForm } from "vee-validate";
import spaceBetween from "@/components/Reusables/divider/spaceBetween.vue";

export default {
  name: "QuestionnaryHomev2",
  components: {
    spaceBetween,
  },
  setup() {
    function onSubmit() {
      alert("Merci pour ce message ! Je te répondrai dès que possible !");
    }
    const validations = {
      textarea: (value) => {
        if (!value) return "Il est nécessaire de répondre à la question";
        return true;
      },
    };
    useForm({
      validationSchema: validations,
    });
    const { value: textarea, errorMessage: textareaError } =
      useField("textarea");
    return {
      textarea,
      textareaError,
      onSubmit,
    };
  },
  data() {
    return {
      questions: [
        "Dans quel état d'esprit te sens tu aujourd'hui?",

        "Quelle est la pièce préférée de ta maison ou appartement ?",
        "Quel est l'endroit où tu te sens le mieux (lieu de vie ou autre) ?",

        "Quel est le plus bel endroit où tu te sois rendue ?",
        "Qu'est ce que tu aimerais changer dans ton intérieur ?",
        "Pourquoi as tu envie de faire ce(s) changement(s) ?",
        "Quel est ton budget pour ce projet ?",
        "Quel est ton style de décoration ?",
        "Quel est ton style de vie ?",
        "De qui te sens tu le (ou la) plus proche dans ta famille, tes ami(e)s ?",
        "Dans l'idéal absolu, comment vois tu ton lieu de vie ?",
      ],
      placeholders: [
        "Par ex: Je me sens dans un état d'esprit joyeux",
        "Par ex: J'adore ma cuisine car elle est lumineuse",
        "Par ex: J'ai adoré le jardin de ma grand-mère",
        "Par ex: J'aimerais avoir beaucoup plus de clarté dans mon intérieur",
        "Par ex: J'ai envie de faire ce changement car je me sens mal dans mon intérieur",
        "Par ex: Je n'ai pas de budget précis, je veux juste avoir une idée",
        "Par ex: J'aime le style scandinave",
        "Par ex: Je suis une personne très active",
        "Par ex: Je suis très proche de ma mère",
        "Par ex: J'aimerais avoir un intérieur très lumineux et très coloré",
      ],
      currentIndex: 0,
    };
  },
  computed: {
    currentQuestion: function () {
      return this.questions[
        Math.abs(this.currentIndex) % this.questions.length
      ];
    },
    currentPlaceholder: function () {
      return this.placeholders[
        Math.abs(this.currentIndex) % this.placeholders.length
      ];
    },
  },
  methods: {
    nextQuestion() {
      this.currentIndex++;
    },
  },
};
</script>
<style scoped>
.internalwrapper {
  @apply flex flex-row content-center justify-center items-center;
}
.upperlabel {
  @apply flex flex-col content-center justify-center items-center;
}
.titrestyle {
  @apply w-full inline-block mb-2 text-bleu-logo text-8xl font-Shintia text-center;
}
.textarea {
  @apply mx-96 resize block w-[1024px] px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-blue-50 focus:border-blue-600 focus:outline-none placeholder:text-black;
}
.validator {
  @apply text-red-500 text-lg font-bold;
}
.labeldesign {
  @apply w-full inline-block mb-2 text-bleu-logo text-8xl font-Shintia;
}
.questionstyle {
  @apply mx-96 resize block w-[1524px] px-3 py-1.5 text-base font-normal text-gray-700 bg-white bg-clip-padding border border-solid border-gray-300 rounded transition ease-in-out m-0 focus:text-gray-700 focus:bg-blue-50 focus:border-blue-600 focus:outline-none;
}
.boutonOrganizer {
  @apply flex flex-row content-center justify-center gap-10 items-center;
}
.boutonstyleone {
  @apply bg-bleu-logo text-white font-bold py-2 px-4 rounded-full align-middle items-end justify-end hover:bg-blue-200 hover:border-solid hover:border-bleu-logo hover:ring-2 hover:text-bleu-logo;
}
</style>
