<template>
  <section class="Maison">
    <div class="maisonwrap">
      <div v-for="i in [currentIndex]" :key="i">
        <img class="image" :src="currentImg" />
      </div>
    </div>

    <div class="Titre">
      <h1>Se connaitre, se respecter, s'épanouir</h1>
    </div>
    <div>
      <AccordionView title="T'accompagner, t'écouter, te soutenir">
        <h1>Tu as besoin :</h1>
        <ul>
          <li>
            d'être entendue dans la confiance, le respect, le non-jugement et la
            confidentialité ?
          </li>
          <li>
            D'un starter pour t'aider à avancer dans ta vie et tes projets
          </li>
          <li>
            De conseils pour t'aider à prendre soin de toi, de ton corps, de ton
            esprit, de ton coeur
          </li>
        </ul>
      </AccordionView>
      <AccordionView title="Te mettre en lien... ">
        <h1>
          Tu as besoin d'être mise en relation face à tes préoccupations avec:
        </h1>
        <ul>
          <li>Des professionnels médicaux et paramédicaux</li>
          <li>
            D'autres parents, pour partager, échanger, se soutenir, se
            comprendre...
          </li>
          <li>
            Des associations, pour t'aider à trouver des réponses à tes
            questions...
          </li>
          <li>
            Mais aussi d'explorer d'autres ressources (Livres, films, sites)
          </li>
        </ul>
      </AccordionView>
    </div>
  </section>
</template>
<script>
import AccordionView from "@/components/accordion/accordionv2.vue";
export default {
  name: "PrestaLifeStyle",
  components: {
    AccordionView,
  },
  data() {
    return {
      images: [
        require("@/assets/img/femme_cheval.jpeg"),
        require("@/assets/img/femme-yoga.jpeg"),
        require("@/assets/img/maman-active.jpeg"),
        require("@/assets/img/femme_active.jpeg"),
      ],
      timer: null,
      currentIndex: 0,
    };
  },

  computed: {
    currentImg: function () {
      return this.images[Math.abs(this.currentIndex) % this.images.length];
    },
  },

  mounted: function () {
    this.startSlide();
  },

  methods: {
    startSlide: function () {
      this.timer = setInterval(this.next, 3000);
    },

    next: function () {
      this.currentIndex += 1;
    },
    prev: function () {
      this.currentIndex -= 1;
    },
  },
};
</script>

