<template>
  <form class="labellingform" @submit.prevent="onSubmit">
    <BaseInput
      v-model="text"
      class="labellingtext"
      label="Quelle est ton nom et prénom?"
      placeholder="Par ex: Julien Dupont"
      type="text"
      :error="textError"
    />
    <BaseInput
      v-model="email"
      class="labellingmail"
      label="Email"
      placeholder="Par ex: juliedupont@tonfournisseur.com"
      type="email"
      :error="emailError"
    />
    <BaseInput
      v-model="textarea"
      class="labellingtextarea"
      label="Ton message"
      placeholder="Par ex: Je suis intéressé par ton projet"
      type="textarea"
      :error="textareaError"
    />

    <BaseButton type="submit" class="bouton"> Submit </BaseButton>
  </form>
</template>

<script>
import { useField, useForm } from "vee-validate";
import BaseButton from "@/components/Reusables/Forms/BaseButton.vue";
import BaseInput from "@/components/Reusables/Forms/BaseInput.vue";
export default {
  name: "BaseForm",
  components: {
    BaseButton,
    BaseInput,
  },
  setup() {
    function onSubmit() {
      alert("Merci pour ce message ! Je te répondrai dès que possible !");
    }
    const validations = {
      email: (value) => {
        if (!value) return "J'ai besoin de ton email pour te répondre";
        const regex =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!regex.test(String(value).toLowerCase())) {
          return "Merci d'entrer une adresse mail valide";
        }
        return true;
      },
      text: (value) => {
        const requiredMessage =
          "Ton nom et ton prénom me seront utiles pour te répondre.";
        if (value === undefined || value === null) return requiredMessage;
        if (!String(value).length) return requiredMessage;
        return true;
      },
      textarea: (value) => {
        const requiredMessage = "Quel est ton message ?";
        if (value === undefined || value === null) return requiredMessage;
        if (!String(value).length) return requiredMessage;
        return true;
      },
    };
    useForm({
      validationSchema: validations,
    });
    const { value: email, errorMessage: emailError } = useField("email");
    const { value: text, errorMessage: textError } = useField("text");
    const { value: textarea, errorMessage: textareaError } =
      useField("textarea");
    return {
      onSubmit,
      email,
      emailError,
      text,
      textError,
      textarea,
      textareaError,
    };
  },
};
</script>
<style>
.bouton {
  @apply bg-bleu-logo text-white font-bold py-2 px-4 rounded;
}
.labellingform {
  @apply flex flex-col items-center justify-center;
}
.labellingtext {
  @apply w-96 m-2 text-center text-bleu-logo font-bold p-4;
}
.labellingmail {
  @apply w-96 m-2 text-center text-bleu-logo font-bold p-4;
}
.labellingtextarea {
  @apply w-96 m-2 text-center text-bleu-logo font-bold p-4;
}
</style>
