<template>
  <div v-show="title == selectedTitle" class="tab-content">
    <slot />
  </div>
</template>

<script>
import { inject } from "vue";
export default {
  props: {
    title: { type: String, required: true },
  },
  setup() {
    const selectedTitle = inject("selectedTitle");
    return {
      selectedTitle,
    };
  },
};
</script>
