<template>
  <section>
    <h1 class="maintitre" data-test="action-phrase">
      <span class="spanner">Si tu veux </span>
      <br class="separator" />
      <span :class="actionClasses">{{ action }} </span>
    </h1>
    <h2 class="secondtitre">
      Ou si tu as simplement... Besoin d'être accompagnée ?
    </h2>
  </section>
</template>
<script>
import NextElementInList from "@/utils/NextElementInList.js";
export default {
  name: "HeadLine",
  data() {
    return {
      action: "Agir sur votre intérieur",
      interval: null,
    };
  },
  computed: {
    actionClasses() {
      return {
        Agir: this.action === "Agir sur ton intérieur",
        Réorganiser: this.action === "Réorganiser ton foyer",
        Simplifier: this.action === "Simplifier ta vie",
        Préparer: this.action === "Préparer son arrivée",
      };
    },
  },
  created() {
    this.ChangeTitle();
  },
  beforeUnmount() {
    clearInterval(this.interval);
  },

  methods: {
    ChangeTitle() {
      this.interval = setInterval(() => {
        const actions = [
          "Agir sur ton intérieur",
          "Réorganiser ton foyer",
          "Simplifier ta vie",
          "Préparer son arrivée",
        ];
        this.action = NextElementInList(actions, this.action);
      }, 3000);
    },
  },
};
</script>
<style scoped>
.maintitre {
  @apply pt-12 font-bold tracking-tighter text-4xl sm:text-4xl md:text-5xl lg:text-7xl text-center mb-10 font-sans;
}
.spanner {
  @apply font-Shintia text-vert-logo;
}
.separator {
  @apply py-20;
}
.secondtitre {
  @apply text-3xl font-light font-Shintia text-bleu-logo text-center pt-10;
}

.Agir {
  @apply text-bleu-logo;
}
.Réorganiser {
  @apply text-vert-logo;
}
.Simplifier {
  @apply text-bleu-logo;
}
.Préparer {
  @apply text-vert-logo;
}
</style>
