<template>
  <div>
    <div class="faqs">
      <FAQ
        v-for="(faq, i) in faqs"
        :key="i"
        :faq="faq"
        :index="i"
        :open="faq.open"
        @toggle-open="toggleOpen"
      />
    </div>
  </div>
</template>

<script>
import FAQ from "@/components/accordion/accordion.vue";
export default {
  name: "AccorDion",
  components: {
    FAQ,
  },
  data() {
    return {
      faqs: [
        {
          question:
            "Puis je offrir cette prestation comme cadeau de naissance?",
          answer:
            "Ce serait un grand plaisir pour moi que de répondre oui. Cependant, ce sera difficilement une surprise. J'ai en effet besoin d'un contact direct avec la personne que je vais aider. Mais tout peut s'envisager ! Appelle-moi !",
          open: false,
        },
        {
          question:
            "Peut-on te contacter par téléphone, j'ai besoin  d'un contact direct?",
          answer:
            "Bien sûr que oui ! N'hésites pas à m'appeler en composant le 0617784649. Je te répondrai avec plaisir ! ",
          open: false,
        },
        {
          question:
            "J'hésite à m'engager car je ne cerne pas vraiùment mes problèmes, comment faire ? ",
          answer:
            "Tu trouveras ci dessus deux boutons pointant vers des questionnaires te permettant de mieux cibler tes problématiques. C'est peut être un bon début ! Après, tu peux aussi me rappeler en composant le 0617784649. Je te répondrai avec plaisir ! ",
          open: false,
        },
      ],
    };
  },
  methods: {
    toggleOpen: function (index) {
      this.faqs = this.faqs.map((faq, i) => {
        if (index === i) {
          faq.open = !faq.open;
        } else {
          faq.open = false;
        }
        return faq;
      });
    },
  },
};
</script>
