.faq {
  @apply bg-white p-4 m-4 w-full max-w-3xl rounded-lg shadow-lg block;
}
.faq .question {
  @apply relative text-xl text-slate-600 transition-all duration-300 ease-in-out after:relative after:top-[50%] after:right-0 after:translate-y-[-50%] after:rotate-0 after:w-8 after:h-8 after:bg-arrow-down-mint after:bg-no-repeat after:bg-center after:bg-contain after:transition-all after:duration-200 after:ease-in-out;
}

.faq.open .question {
  @apply mb-4 after:rotate-90 after:translate-y-[-50%];
}
.faq .answer {
  @apply text-slate-600 text-xl max-h-0 overflow-hidden transition-all duration-300 ease-in-out;
