<template>
  <div class="tabs">
    <ul class="tabs_header">
      <li
        v-for="title in tabTitles"
        :key="title"
        :class="{ selected: title == selectedTitle }"
        @click="selectedTitle = title"
      >
        {{ title }}
      </li>
    </ul>
    <slot />
  </div>
</template>
<script>
import { ref, provide } from "vue";
export default {
  setup(props, { slots }) {
    const tabTitles = ref(slots.default().map((tab) => tab.props.title));
    const selectedTitle = ref(tabTitles.value[0]);
    provide("selectedTitle", selectedTitle);
    return {
      selectedTitle,
      tabTitles,
    };
  },
};
</script>
<style scoped>
.tabs {
  @apply w-auto flex flex-col justify-center items-center;
}
.tabs_header {
  @apply flex flex-col justify-center items-center gap-4 flex-wrap mx-10 md:flex-row;
}
.tabs_header li {
  @apply text-3xl ring-1 rounded-xl p-10 shadow-md justify-between drop-shadow-2xl cursor-pointer;
}
.tabs_header li.selected {
  @apply bg-bleu-logo;
}
</style>
