<template>
  <header :class="{ 'scolled-nav': scrollPosition }">
    <div>
      <LogoPlacer />
    </div>
    <nav>
      <div class="branding">
        <img src="@/assets/img/logo2.png" alt="" />
      </div>
      <ul v-show="!mobile" class="navigation">
        <li>
          <router-link class="link" to="/me-connaitre"
            >Me connaitre</router-link
          >
        </li>
        <li>
          <router-link class="link" to="/prestations">Prestations</router-link>
        </li>
        <li>
          <router-link class="link" to="/Realisations"
            >Realisations</router-link
          >
        </li>
        <li>
          <router-link class="link" to="/tarifs">Tarifs</router-link>
        </li>
        <li>
          <router-link class="link" to="/contact">Contact</router-link>
        </li>
      </ul>
      <div class="icon">
        <font-awesome-icon
          v-show="mobile"
          class="iconfabars"
          :class="{ 'icon-active': mobileNav }"
          icon="fa-solid fa-bars"
          @click="toggleMobileNav"
        ></font-awesome-icon>
      </div>
      <transition name="mobile-nav">
        <ul v-show="mobileNav" class="dropdown-nav">
          <li>
            <router-link class="link" to="/me-connaitre"
              >Me connaitre</router-link
            >
          </li>
          <li>
            <router-link class="link" to="/prestations"
              >Prestations</router-link
            >
          </li>
          <li>
            <router-link class="link" to="/Realisations"
              >Realisations</router-link
            >
          </li>
          <li>
            <router-link class="link" to="/tarifs">Tarifs</router-link>
          </li>
          <li>
            <router-link class="link" to="/contact">Contact</router-link>
          </li>
        </ul>
      </transition>
    </nav>
  </header>
</template>

<script>
import LogoPlacer from "@/components/Navigation/NavAnimation/TopHeaderTwo.vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
export default {
  name: "MainNavOp",
  components: { LogoPlacer, FontAwesomeIcon },
  data() {
    return {
      scrollPosition: null,
      mobile: null,
      mobileNav: null,
      windowWidth: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  methods: {
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },
    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 750) {
        this.mobile = true;
        return;
      } else {
        this.mobile = false;
        this.mobileNav = false;
        return;
      }
    },
  },
};
</script>
